import React from 'react';
import {createNativeStackNavigator} from '@react-navigation/native-stack';
import SmartTestListAppScreen from '../screens/SmartTestListAppScreen';
import ClassManageScreen from '../screens/ClassManageScreen';
import ExamManageScreen from '../screens/ExamManageScreen';
import QuestionManageScreen from '../screens/QuestionManageScreen';
import ALEVELAppScreen from '../screens/Applications/ALEVELAppScreen';
import SASMOAppScreen from '../screens/Applications/SASMOAppScreen';
import IGCSEAppScreen from '../screens/Applications/IGCSEAppScreen';
import ToanPTAppScreen from '../screens/Applications/ToanPTAppScreen';

export type ListAppsStackParamList = {
  SmartTestListAppScreen: undefined;
  ClassManageScreen: undefined;
  ExamManageScreen: undefined;
  QuestionManageScreen: undefined;
  ALEVELAppScreen: undefined;
  SASMOAppScreen: undefined;
  IGCSEAppScreen: undefined;
  ToanPTAppScreen: undefined;
};

const ListApp = createNativeStackNavigator<ListAppsStackParamList>();

const ListAppNavigator = () => {
  return (
    <ListApp.Navigator
      screenOptions={{
        headerShown: false,
      }}>
      <ListApp.Screen
        name="SmartTestListAppScreen"
        component={SmartTestListAppScreen}
      />
      <ListApp.Screen name="ClassManageScreen" component={ClassManageScreen} />
      <ListApp.Screen name="ExamManageScreen" component={ExamManageScreen} />
      <ListApp.Screen
        name="QuestionManageScreen"
        component={QuestionManageScreen}
      />
      <ListApp.Screen name="ALEVELAppScreen" component={ALEVELAppScreen} />
      <ListApp.Screen name="SASMOAppScreen" component={SASMOAppScreen} />
      <ListApp.Screen name="IGCSEAppScreen" component={IGCSEAppScreen} />
      <ListApp.Screen name="ToanPTAppScreen" component={ToanPTAppScreen} />
    </ListApp.Navigator>
  );
};

export default ListAppNavigator;
